  }

  if (!formData.ai_parameter_id) {
    formErrors.ai_parameter_id = 'ËØ∑ÈÄâÊã©Êô∫Ê†∏ÂèÇÊï∞'
    valid = false
  }

  if (!formData.status) {
    formErrors.status = 'ËØ∑ÈÄâÊã©Áä∂ÊÄÅ'
    valid = false
  }

  return valid
}

// Êñ∞Â¢û
function handleAdd() {
  modalType.value = 'add'
  formData.name = ''
  formData.product_code = ''
  formData.product_type_id = undefined
  formData.insurance_company_id = undefined
  formData.channel_id = undefined
  formData.ai_parameter_id = undefined
  formData.status = 'enabled'
  showModal.value = true
}

// ÁºñËæë
function handleEdit(item: Product) {
  modalType.value = 'edit'
  currentId.value = item.id
  formData.name = item.name
  formData.product_code = item.code
  formData.product_type_id = item.productType?.id
  formData.insurance_company_id = item.insuranceCompany?.id
  formData.channel_id = item.channel?.id
  formData.ai_parameter_id = item.aiParameter?.id
  formData.underwriting_rule = item.aiParameter?.rule?.name || ''
  formData.status = item.status
  showModal.value = true
}

// ÂºπÁ™óÁ°ÆËÆ§
async function handleModalConfirm() {
  if (!validateForm()) return

  try {
    if (modalType.value === 'add') {
      await handleCreate()
    } else {
      await handleUpdate()
    }
  } catch (error) {
    console.error('‰øùÂ≠òÂ§±Ë¥•:', error)
    message.error('‰øùÂ≠òÂ§±Ë¥•ÔºåËØ∑ÈáçËØï')
  }
}

// ÂàõÂª∫‰∫ßÂìÅ
async function handleCreate() {
  try {
    const res = await post<ApiResponse<any>>('/api/v1/business/products', {
      name: formData.name,
      product_code: formData.product_code,
      product_type_id: formData.product_type_id,
      insurance_company_id: formData.insurance_company_id,
      channel_id: formData.channel_id,
      ai_parameter_id: formData.ai_parameter_id,
      status: formData.status
    })
    
    message.success('ÂàõÂª∫ÊàêÂäü')
    showModal.value = false
    loadData()
  } catch (error: any) {
    console.error('ÂàõÂª∫‰∫ßÂìÅÂ§±Ë¥•:', error)
    message.error(error.message || 'ÂàõÂª∫Â§±Ë¥•ÔºåËØ∑ÈáçËØï')
  }
}

// Êõ¥Êñ∞‰∫ßÂìÅ
async function handleUpdate() {
  if (!currentId.value) return
  
  try {
    const res = await put<ApiResponse<any>>(`/api/v1/business/products/${currentId.value}`, {
      name: formData.name,
      product_code: formData.product_code,
      product_type_id: formData.product_type_id,
      insurance_company_id: formData.insurance_company_id,
      channel_id: formData.channel_id,
      ai_parameter_id: formData.ai_parameter_id,
      status: formData.status
    })
    
    message.success('Êõ¥Êñ∞ÊàêÂäü')
    showModal.value = false
    loadData()
  } catch (error: any) {
    console.error('Êõ¥Êñ∞‰∫ßÂìÅÂ§±Ë¥•:', error)
    message.error(error.message || 'Êõ¥Êñ∞Â§±Ë¥•ÔºåËØ∑ÈáçËØï')
  }
}

// Âà†Èô§‰∫ßÂìÅ
async function handleDelete(item: Product) {
  try {
    await message.confirm(`Á°ÆËÆ§Âà†Èô§‰∫ßÂìÅ"${item.name}"ÂêóÔºü`)
    const res = await del<ApiResponse<any>>(`/api/v1/business/products/${item.id}`)
    
    message.success('Âà†Èô§ÊàêÂäü')
    loadData()
  } catch (error: any) {
    if (error === 'cancel') return
    console.error('Âà†Èô§‰∫ßÂìÅÂ§±Ë¥•:', error)
    message.error(error.message || 'Âà†Èô§Â§±Ë¥•ÔºåËØ∑ÈáçËØï')
  }
}

// ‰øÆÊîπÊô∫Ê†∏ÂèÇÊï∞ÂèòÊõ¥Â§ÑÁêÜÂáΩÊï∞
async function handleAiParameterChange(value: number | undefined) {
  console.log('[Êô∫Ê†∏ÂèÇÊï∞] ÂèÇÊï∞ÂèòÊõ¥:', value);
  formData.underwriting_rule = '';  // Ê∏ÖÁ©∫ÂéüÊúâËßÑÂàô
  
  if (!value) {
    return;
  }

  try {
    // ‰ªéÂ∑≤Âä†ËΩΩÁöÑAIÂèÇÊï∞ÈÄâÈ°π‰∏≠Êü•ÊâæÂØπÂ∫îÁöÑËßÑÂàô
    const selectedParam = aiParameterOptions.value.find(option => option.value === value);
    if (selectedParam) {
      const paramData = await get(`/api/v1/underwriting/ai-parameter/${value}`);
      console.log('[Êô∫Ê†∏ÂèÇÊï∞] Ëé∑ÂèñËßÑÂàô‰ø°ÊÅØ:', paramData);
      
      if (paramData.data?.rule?.name) {
        formData.underwriting_rule = paramData.data.rule.name;
      }
    }
  } catch (error) {
    console.error('[Êô∫Ê†∏ÂèÇÊï∞] Ëé∑ÂèñËßÑÂàô‰ø°ÊÅØÂ§±Ë¥•:', error);
  }
}

